.SignupPage {
    background-color: #476F9D;
    height: 100vh;
    width: 100vw;
}

.signUpBackground {
    background-color: white;
    padding: 80px;
    margin-bottom: 10px;
    
}

.btn, .btn-default {
    background-color: #476F9D;
}

/* input .form-control {
    width: 15vw;
} */

.btn, .btn-default, .signUpBtn {
    background-color: #476F9D;
    border: 0 solid;
    -webkit-box-shadow: inset 0 0 20px rgba(255, 255, 255, 0);
            box-shadow: inset 0 0 20px rgba(255, 255, 255, 0);
    outline: 1px solid;
    outline-color: rgba(255, 255, 255, .5);
    outline-offset: 0px;
    text-shadow: none;
    -webkit-transition: all 1250ms cubic-bezier(0.19, 1, 0.22, 1);
    -o-transition: all 1250ms cubic-bezier(0.19, 1, 0.22, 1);
    transition: all 1250ms cubic-bezier(0.19, 1, 0.22, 1);
}

.btn:hover, .btn-large:hover {
    background-color: #476F9D;
    border: 1px solid;
    -webkit-box-shadow: inset 0 0 20px rgba(255, 255, 255, .5), 0 0 20px rgba(255, 255, 255, .2);
            box-shadow: inset 0 0 20px rgba(255, 255, 255, .5), 0 0 20px rgba(255, 255, 255, .2);
    outline-color: rgba(255, 255, 255, 0);
    outline-offset: 15px;
    text-shadow: 1px 1px 2px #427388; 
}

.cancelBtn {
    color: black;
}


